<template>
  <div class="page" :class="getPageClass()">
    <headerNav title="模型结果" />

    <ul>
      <van-list
        v-model="vantList.loading"
        :finished="vantList.finished"
        @load="onLoad"
      >
        <li
          v-for="(item, index) in vantList.list"
          :key="index"
          :class="(vantList.loading ? 'van-li-hide' : '')"
        >
          <div class="item-name">
            <span>{{ item.title }}</span>
          </div>
          <div class="item-coupon">
            <div class="item-img">
              <!-- <img src="../assets/images/product-1.png" /> -->
              <img :src="item.model" />
            </div>

            <div class="item-cpninfo">
              <div class="line-box">
                <div
                  class="line"
                  v-for="(subitem, subindex) in item.area"
                  :key="subindex"
                  @click="onClick(item, index, subitem, subindex)"
                  :class="setclass(item, index, subitem, subindex)"
                ></div>
              </div>
            </div>
          </div>
        </li>
      </van-list>
    </ul>

    <van-button
      type="primary"
      size="large"
      @click="goPage('/result', 'result', vantList.list[0])"
      >查看结果</van-button
    >

    <van-notice-bar
      :color="noticeBar.color"
      :background="noticeBar.background"
      >{{ noticeBar.text }}</van-notice-bar
    >
  </div>
</template>


<script>
import { getProduct } from "../api/page.js";

export default {
  data() {
    return {
      vantList: {
        productid: '',
        loading: false,
        finished: false,
        list: [],
      },
      noticeBar: {
        color: "#1989fa",
        background: "#ecf9ff",
        text: "请点击模型CT位置模拟您的测试结果",
      },
        // route:{

        // }
    };
  },
  computed: {},
  created() {
    // console.log( this.$route )
     this.vantList.productid = this.$route.params.id;

  },
watch: {
    $route: {
      handler: function(route) {
        console.log( route )
        this.vantList.productid = this.$route.params.id;
        this.vantList.loading = true;
        this.vantList.finished = false;
        this.onLoad();
      },
      immediate: true
    }
  },

  methods: {
    onLoad() {
      if(! this.vantList.productid ){
            return;
      }
      getProduct({ id: this.vantList.productid }).then((response) => {
        this.vantList.list = [];

        response.area = [];
        response.unit.map((item) => {
          response.area.push(
            { label: "c" + item, flag: false },
            { label: "t" + item, flag: false }
          );
        });
        this.vantList.list.push(response);

        this.vantList.loading = false;
        this.vantList.finished = true;
      });
    },

    onClick(item, index, subitem, subindex) {
      this.vantList["list"][index]["area"][subindex]["flag"] =
        !this.vantList["list"][index]["area"][subindex]["flag"];
    },
    goPage(pagepath, pagename, params) {
      this.$router.push({ name: pagename, params: params });
    },
    setclass(item, index, subitem, subindex) {
      return [
        "line-" + subitem.label,
        this.vantList["list"][index]["area"][subindex]["flag"] ? "checked" : "",
      ];
    },
    getPageClass() {
      return ["page-" + this.vantList.productid];
    },
  },
};
</script>
